import './board.css';
import {Frame, Banner, ContentNav, Footer} from '../frame/frame.js';
import axios from 'axios';
import { Component } from 'react';

export default class Writing extends Component{
    state = {
        title : '',
        contents : '',
    }

    changeTitle = (event) => {
        this.setState({title:event.target.value})
    }

    changeArea = (event) => {
        this.setState({contents:event.target.value})
    }
    
    writeContent = async () =>{
        const dataSet ={
            title : this.state.title,
            contents : this.state.contents,
        };
    
        const axiosConfig = {
            headers: {
                Authorization: `Bearer ${localStorage.getItem('jwtToken')}`,
            },
        };    
        const res = await axios.post('/board/save',dataSet,axiosConfig).then(()=>{
            this.props.history.push('/board');
        });

    }

    render(){
    return (
        <div>
            <Frame/>
            <Banner name={''}/>
            <ContentNav name={''}/>
            <div className='communityContentBox'>
                <h1></h1>
                <div className="formBox">
                        <div>
                            <label></label><br/><br/>
                            <input 
                            type="text"
                            id="contentSubject"
                            placeholder=' '
                            onChange = {this.changeTitle}
                            value = {this.state.title}
                            required
                            /><br/><br/><br/>
                        </div>

                        <div>
                            <label></label><br/><br/>
                            <textarea 
                            id="contentTextBox"
                            placeholder=' '
                            onChange={this.changeArea}
                            value = {this.state.contents}
                            required
                            />
                        <br/><br/><br/>
                        </div>

                        <div>
                            <label> </label><br/><br/>
                            <input type="text"
                            placeholder='10Mb jpg, png   .'
                            disabled
                            />
                            <button className="btnSubmit" id="btnFile"></button>
                            <button className="btnSubmit btnWhite"></button>
                            <br/><br/><br/><br/>
                        </div>
                </div>
                <div className="submit">
                    <button className ="btnSubmit" onClick={this.writeContent}></button>
                    <button className ="btnSubmit btnWhite" onClick={()=>{window.location.href="/board"}}></button>
                </div>
            </div>
            <Footer/>
        </div>

    );
    }
}